.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.bg-couple {
		position: relative;
		width: 100%;
		@include max-md {
			aspect-ratio: 1;
			.image-element {
				height: 100%;
				.img-fluid {
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.title-section {
		position: absolute;
		left: 50%;
		top: 5rem;
		transform: translate(-50%, 0);
	}
	.couple-wrapper {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/9;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 300px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
		&.man {
			text-align: left;
			.image-wrap,
			.sosmed-wrap {
				margin-right: auto;
			}
		}
		&.woman {
			text-align: right;
			.image-wrap,
			.sosmed-wrap {
				margin-left: auto;
			}
		}
	}
}
