.thank-section {
	position: relative;
	padding: 5rem 0 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		margin: 0 auto;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding-bottom: 12rem;
		@include max-md {
			padding-bottom: 10rem;
		}
		.elements-wrapper-absolute {
			.background-element {
				img {
					object-position: 50% 50%;
				}
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			background: var(--bg-gradient);
			color: var(--color-dark);
			box-shadow: 0.5rem 0.5rem 2rem rgba($color: #000000, $alpha: 0.15);
			border-radius: var(--border-radius);
			margin-top: -5rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
