$color-main: #e8dbc9;
$color-primary: #fcf6f1;
$color-secondary: #704a1e;
$color-dark: #1e1f1e;
$color-light: #fff;
$text-button: #ffffff;
$color-button-trans: #1e1f1e;
$color-logo: #e9088a;
$font-secondary: "Roustel Reguler", cursive;
$font-primary: "Nunito Sans", sans-serif;
$border-radius: 20px;
$bg-gradient: linear-gradient(180deg, var(--color-primary) 30%, transparent 100%);

$breakpoints-min: (
	xs: 325px,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1440px,
	4k: 2560px,
);

$breakpoints-max: (
	xs: 375px,
	sm: 575px,
	md: 767px,
	lg: 991px,
	xl: 1199px,
	xxl: 1399px,
);
