.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		background: var(--bg-gradient);
		border-radius: var(--border-radius);
		padding: 3rem;
		margin-top: -10rem;
		box-shadow: 0.5rem 0.5rem 2rem rgba($color: #000000, $alpha: 0.15);
		@include max-md {
			padding: 2rem 1rem;
			margin-top: -5rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
