.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 45%;
		left: 0;
		width: 380px;
		transform: translate(-40%, -50%);
		@include max-lg {
			top: 60%;
		}
		@include max-md {
			width: 280px;
			transform: translate(-50%, -50%);
		}
	}
	.orn-2 {
		position: absolute;
		top: 40%;
		right: 0;
		width: 420px;
		transform: translate(40%, -50%) scaleX(-1);
		@include max-lg {
			top: 55%;
		}
		@include max-md {
			width: 320px;
			transform: translate(50%, -50%) scaleX(-1);
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6.5rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				width: 35%;
				border-radius: var(--border-radius);
				box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
				@include max-xl {
					width: 40%;
				}
				@include max-lg {
					width: 70%;
				}
				@include max-md {
					width: 100%;
				}
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: linear-gradient(to bottom, rgba(255, 255, 255, 0.8) 0%, transparent 100%);
					content: "";
					border-radius: var(--border-radius);
				}
				.greeting-wrapper {
					position: absolute;
					top: 2rem;
					left: 2rem;
					width: 100%;
					text-align: left;
					h6 {
						font-weight: 700;
					}
				}
				.image-wrapper {
					position: relative;
					width: 100%;
					height: auto;
					aspect-ratio: 8/6;
					overflow: hidden;
					border-radius: var(--border-radius);
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: var(--border-radius);
					}
					.overlay-elements-gradient {
						.overlay-bottom {
							background: linear-gradient(to bottom, var(--color-main) 0%, transparent 100%);
						}
					}
				}
				.cover-description {
					position: relative;
					padding: 2rem;
					text-align: left;
					@include max-md {
						padding: 2rem 1rem;
					}
					p {
						margin-bottom: 0;
					}
					h6 {
						font-weight: 600;
						margin-bottom: 0;
					}
					.title {
						padding: 1rem 0;
						margin-bottom: 0;
					}
					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
