.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	background: var(--bg-gradient);
	color: var(--color-dark);
	box-shadow: 0.5rem 0.5rem 2rem rgba($color: #000000, $alpha: 0.15);
	@include max-md {
		padding: 3rem 0;
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
				.elements-wrapper-absolute {
					opacity: 0.25;
					.background-element {
						img {
							border-radius: var(--border-radius);
							object-position: 50% 100%;
						}
					}
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
	}
}
